/**
 * 
 */
const project =(data)=> {
  return new Promise((resolve, reject) => {
    httpPost(URL_STAT + '/project', data)
      .then(res => {
        resolve (res);
      })
      .catch(err => {
        reject(err)
      })
  }) 
}

/**
 * 
 */
const weekDetails =(id)=>{
  return new Promise((resolve, reject) => {
    httpGet(URL_STAT + '/stat/week/'+id)
      .then(res => {
        resolve (res);
      })
      .catch(err => {
        reject(err)
      })
  }) 
}

